<template>
  <div class="bg8">
    <el-form
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="120px"
      class="ruleForm8"
    >
      <el-form-item>
        <h1></h1>
      </el-form-item>
      <el-form-item label="" prop="userName">
        <el-input v-model="ruleForm.userName" placeholder="userName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="nickName">
        <el-input v-model="ruleForm.nickName" placeholder="nickName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          v-model="ruleForm.password"
          type="password"
          placeholder="password"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="code">
        <el-input v-model="ruleForm.code" class="code"></el-input
        ><el-button
          type="primary"
          class="getCodeBut"
          @click="getCode"
          :disabled="isable"
          ></el-button
        >
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="register"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        userName: "",
        password: "",
        nickName: "",
        code: ""
      },
      isable: false,
      rules: {
        userName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            pattern: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
            message: ""
          }
        ],
        nickName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            min: 3,
            max: 18,
            message: "3-18",
            trigger: "blur"
          }
        ],
        code: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            min: 4,
            max: 4,
            message: "4",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    getCode() {
      //
      this.isable = true;
      this.$notify({
        title: "",
        // html
        dangerouslyUseHTMLString: true,
        message: "<text style='color:#058282'>10s~</text>",
        // 0 >0
        duration: 3 * 1000,
        // 
        type: "success"
      });
      // 
      var pattern = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
      var flag = pattern.test(this.ruleForm.userName);
      //
      if (this.ruleForm.userName != null && flag) {
        let param = new URLSearchParams();
        param.append("userName", this.ruleForm.userName),
          this.$axios
            .post("/user/getCode", param)
            .then(res => {
              if (res.data.code == 200) {
                this.$notify({
                  title: "",
                  // html
                  dangerouslyUseHTMLString: true,
                  message:
                    "<text style='color:#058282'></text>",
                  // 0 >0
                  duration: 3 * 1000,
                  // 
                  type: "success"
                });
              }
              //
              this.isable = false;
            })
            .catch(error => {
              console.info(error);
              //
              this.isable = false;
            });
      } else {
        this.$notify({
          title: "",
          // html
          dangerouslyUseHTMLString: true,
          message: "<text style='color:red'></text>",
          // 0 >0
          duration: 3 * 1000,
          // 
          type: "warning"
        });
        //
        this.isable = false;
      }
    },
    register() {
      let param = new URLSearchParams();
      param.append("userName", this.ruleForm.userName),
        param.append("password", this.ruleForm.password),
        param.append("nickName", this.ruleForm.nickName),
        param.append("code", this.ruleForm.code),
        this.$axios
          .post("/user/register", param)
          .then(res => {
            if (res.data.code != 200) {
              //
              this.$notify({
                title: "",
                // html
                dangerouslyUseHTMLString: true,
                message: "<text style='color:red'>" + res.data.msg + "</text>",
                // 0 >0
                duration: 3 * 1000,
                // 
                type: "warning"
              });
            } else {
              //
              this.$notify({
                title: "Success",
                // html
                dangerouslyUseHTMLString: true,
                message: "<text style='color:#058282'>~</text>",
                // 0 >0
                duration: 3 * 1000,
                // 
                type: "success"
              });
              this.$router.push("/");
            }
          })
          .catch(err => {
            console.error(err);
          });
    }
  }
};
</script>

<style>
.bg8 {
  background: url("http://120.79.198.192:9003/school/2021-10-02/fangzi.jpg")
    no-repeat top;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.ruleForm8 {
  max-width: 460px;
  margin: 10% auto;
}
.code {
  max-width: 230px;
}
.getCodeBut {
  margin-left: 10px;
}
</style>



